
import {
   Link, NavLink
 }from "react-router-dom";
/* style navpar */
import './stylscompawnds/Stylenavpar.scss';
/*--------------*/

function navpar(){
    return (
        <div className="style-navpar">
            <div className="navpar header">
                <div className="content flex-nav">
                    <div><Link to="/home">home</Link></div>
                    <div><Link to="/frinds">frinds</Link></div>
                    <div><NavLink to="/home">stings</NavLink></div>
                    <div><Link to="/printdata">out data</Link></div>
                </div>
            </div>
        </div>
    );
}
export default navpar;