import React from 'react';
import {Link }from "react-router-dom";
import {useState} from 'react';
import { useEffect } from 'react';
import '../styles/sass-pages/Stylesingup.css';
import Navparsing from './compawnds/Navsing';
import {createUserWithEmailAndPassword,updateProfile,sendEmailVerification} from "firebase/auth";
 
import { useNavigate } from "react-router-dom";
import { useContext } from 'react';
import { Filecontext } from '../conText/dataTemth';
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth } from "../firbase/config";
function Singup(){
    const [user, loading, error] = useAuthState(auth);
    const [Email,setemail] = useState("");
    const [Password,setpassword] = useState("");
    const [userName,getusername] = useState("");
    const navigate = useNavigate();
    const {actionsign} = useContext(Filecontext);
    
if(loading)
{
  return (<>please</>);
  
}
if(user)
{
  if(!user.emailVerified)
  {
    return (
      <div className='style-veremail'>
        <div className='flex'>
        <p>     </p>
        <p>{user.email}</p>
        <br></br>
        <button onClick={()=>{document.location.reload();}}> </button>
        </div>
        
      </div>
    );
  }
  if(user.emailVerified)
  {
    navigate("/html");
    actionsign(true);
  }

}
  if(!user) {
    return (
        <div className="style-singup">
            <Navparsing />
            <div className='absolute-form'>
                <form>
                    <h2>elpahr sing up</h2>
                    <input type="text" placeholder='username'onChange={(eo)=>{getusername(eo.target.value)}}/>
                    <br/>
                    <br/>
                    <input type="text" placeholder="Email enter" onChange={(eo)=>{setemail(eo.target.value)}}/>
                    <br/>
                    <br/> 
                    <input type="password" placeholder="password enter"  onChange={(eo)=>{setpassword(eo.target.value)}}/>
                    <br/>
                    <br/>
                    
                    <button onClick={(eo)=>{
                    eo.preventDefault();

createUserWithEmailAndPassword(auth, Email, Password)
  .then((userCredential) => {
    // Signed in 
    //const user = userCredential.user;
    sendEmailVerification(auth.currentUser)
  .then(() => {
    // Email verification sent!
    // ...
  });
    updateProfile(auth.currentUser, {
      displayName: userName,
      
    }).then(() => {
      // Profile updated!
      // ...
    }).catch((error) => {
      // An error occurred
      // ...
    });

    // ...
  })
  .catch((error) => {
    //const errorCode = error.code;
    //const errorMessage = error.message;
    alert("   ")
    // ..
  });}}>  </button>
                    <br/>
                    <p>dont have an acount<Link to="/singin">Sing-In</Link> </p>
                </form>
            </div>
        </div>
    );
  }
} 

export default Singup;