/* export style frinds */
import '../styles/sass-pages/Stylefrinds.scss';
/*--------------------*/
/* import compawnds  */
import Navpar from '../pages/compawnds/navpar';
import Modile from './compawnds/modile';
/*------------------*/
function frinds(){
    return (
        <div className="style-frinds">
            <Navpar />
            <Modile select_namee={" frinds"}/>
        </div>
    );
}
export default frinds;