import React from 'react';
import {createContext} from 'react';
import {useReducer} from 'react';
export const Filecontext = createContext();



const mydata = {
    myname:"ahmed fathe",
    themth:localStorage.getItem("themm") ===null?"":localStorage.getItem("themm"),
    myage:20,
    count:0,
    menue:"none", 
    acountsign:localStorage.getItem("sign") === null ? false :localStorage.getItem("sign"),
    };
function reducer(state,action){
switch(action.type){
case "CHILDMENUE":
    return {...state,menue:action.newValue};
case "childthemth":
        return {...state,themth:action.newValue};
case "childage":
        return {...state,myage:action.newValue};
case "changecount":
        return {...state,count:action.newValue};
case "changename":
    return {...state,myname:action.newValue};
case "changesign":
    return {...state,acountsign:action.newValue};
    default:
        return state;
}
}




const Filecontextprovider = (pramter)=>{
const [allData,dispatch] = useReducer(reducer,mydata);
let actionname = ()=>{
    dispatch({type:"changename",newValue:"ahmed Elpahr"});
}
let actionage = ()=>{
    dispatch({type:"childage",newValue:24});
}

let actionmenue = ()=>{
    dispatch({type:"CHILDMENUE",newValue:allData.menue ==="none"?"block":"none"});
}

let actionthemth = (newthemth)=>{
    localStorage.setItem("themm",newthemth);
    dispatch({type:"childthemth",newValue:newthemth});
}

let actioncount = ()=>{
    dispatch({type:"changecount",newValue:allData.count+10});
}
let actiontoglethemth = (togle)=>{
    localStorage.setItem("themm",togle);
    dispatch({type:"childthemth",newValue:togle});
}
let actionsign = (acount)=>{
    localStorage.setItem("sign",acount);
    dispatch({type:"changesign",newValue:acount});
}


    return (
        <Filecontext.Provider value={{
            allData,
            actionage,
            actioncount,
            actionmenue,
            actionname,
            actionthemth,
            dispatch,
            actiontoglethemth,
            actionsign,
            
        }}>
            {pramter.children}
        </Filecontext.Provider>
    );
    
}
export default Filecontextprovider;