import React from 'react';
import '../styles/sass-pages/Profile.css';
import Navparpages from './compawnds/navpaepages';
import { useAuthState } from 'react-firebase-hooks/auth';
import {auth} from "../firbase/config";
import {deleteUser } from "firebase/auth";
import { useEffect,useState } from 'react';
import Moment from 'react-moment';
import { useNavigate } from "react-router-dom";
function Profile(){
    const [user, loading, error] = useAuthState(auth);
    const navigate = useNavigate();
    
         if(!user)
         {
            navigate("/singin");
         }
        if(loading){
            return(
                <>
                <div className='style-profile-fixed-loding'>
                    <div className='loade'>
                        
                    </div>
                </div>
                <Navparpages />

                </>
            );
        }
        if (error) {
            return (
              <div>
                <p>Error: {error}</p>
              </div>
            );
          }

    if(user){
        return(
            <div className="style-profile">
            <Navparpages />
            <div className='content'>
            <div className='flex-profile'>
             

            <div className="avtar-acount">
                   <div className='acount-img'>
                    
                   </div>
                   <div className='contentt'>
                    <p>{user.displayName}</p>
                   </div>
            </div>
                <div className='content-acount'>
                     <p>infoemation</p>
                    <div className='information-acount'>
                    <table>
                        <thead>
                        <tr className='title'>
                                <td>email</td>
                                <td>phone</td>
                            </tr>
                            <tr>
                                <td>{user.email}</td>
                                <td>32</td>
                            </tr>
                            <tr className='title'>
                                <td>acount created</td>
                                <td>usename</td>
                            </tr>
                            <tr>
                                <td><Moment fromNow date={user.metadata.lastSignInTime} /></td>
                                <td>{user.displayName}</td>
                            </tr>
                            <tr><td><button className='delete-acount' onClick={()=>{
                                deleteUser(user).then(() => {
                                    // User deleted.
                                  }).catch((error) => {
                                    // An error ocurred
                                    // ...
                                  });
                                  
                            }}>deleate-acount</button></td></tr>
                        </thead>
                    </table>
                    </div>
                </div>



            </div>

            </div>
           
        </div>
        )
    }


}

export default Profile;