import React from 'react';
import './Styleapp.css';
import {
    createBrowserRouter,
    RouterProvider,
   /*Route,*/
   
   
 }from "react-router-dom";
/* pages */
import Home from './pages/home';
import Frinds from './pages/frinds';
import Printdate from './pages/printdata';
import HTML from './pages/Html';
import Css from './pages/Css';
import Javascript from './pages/Javascript';
import Singin from './pages/Singin';
import Singup from './pages/Singup';
import Profile from './pages/Profile';

const router = createBrowserRouter([
    {path:"/",
    element:<Singin />,
    errorElement:<h1>   </h1>
    },
    
    {
      path:"/home",
      element:<Home />,
    },
    {
      path:"/frinds",
      element:<Frinds />,
    },
    {
      path:"/printdata",
      element:<Printdate />,
    },
    {
        path:"/html",
        element:<HTML />,
    },
    {
        path:"/css",
        element:<Css />,
    },
    {
        path:"/java",
        element:<Javascript />
    },
    {
      path:"/singin",
      element:<Singin />
    },
    {
      path:"/singup",
      element:<Singup />,
    },
    {
      path:"/profile",
      element:<Profile />,
    }
  
  
  ]);

function App()
{   
    
    return ( 
        
        <div className={`App`}>
            <RouterProvider router={router} />
        </div>
        );   
}
export default App;