import React from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth } from '../firbase/config';
import '../styles/sass-pages/Stylecss.css'
import Navparpages from './compawnds/navpaepages';
import { useNavigate } from "react-router-dom";
function Css(){
    const [user, loading, error] = useAuthState(auth);
    const navigate = useNavigate();
    if(loading)
    {
        return (<>
            <div className='style-profile-fixed-loding'>
                <div className='loade'>
                    
                </div>
            </div>
            <Navparpages /></>);
    }
    if(user)
    {
        if(user.emailVerified)
        {
            return (
            <div className="style-css">
                 <Navparpages />
                <h1>css</h1>
            </div>
            );
        }
    }
    
    if(!user)
    {
        return (
            navigate("/singin")
        );
    }
    
}
export default Css;