import './stylscompawnds/Stylenavparpages.css';
import {NavLink} from 'react-router-dom';
import { Filecontext } from '../../conText/dataTemth';
import {useContext,useEffect} from 'react';
import { signOut } from "firebase/auth";
import {auth} from '../../firbase/config';
import { useNavigate } from "react-router-dom";
function Navparpages()
{ 
    const {allData,actionthemth,actionmenue,actionsign} = useContext(Filecontext);
    const navigate = useNavigate();
    

    document.body.className=allData.themth;
    
    return (
        
        <div className={`navpar-pages ${allData.themth} header`}>
            <div className="navpar content">
            
                <div className="logo"><p>elpahr</p></div>
                <div className="but-dark" onClick={()=>{actionthemth(allData.themth ==="Dark" ? "light" :"Dark")}}><p>dark</p></div>
                <div className="flex-buttons">
                    <div><p><NavLink to="/html">html</NavLink></p></div>
                    <div><p><NavLink to="/css">css</NavLink></p></div>
                    <div><p><NavLink to="/java">jsvascript</NavLink></p></div>
                    <div><p><NavLink to="/profile">profile</NavLink></p></div>
                    <div className='singout'><p onClick={()=>{
                        signOut(auth).then(() => {
                            navigate("/Singin");
                            actionsign(false);
                            
                          }).catch((error) => {
                            // An error happened.
                          });
                    }}>sin-out</p></div>
                </div>
                <div className='navpar-mobile'>
                    <p onClick={()=>{
                        actionmenue();
                    }}>MO</p>
                    <div className={`menue ${allData.menue}`}>
                
                    <div><p><NavLink to="/html">html</NavLink></p></div>
                    <div><p><NavLink to="/css">css</NavLink></p></div>
                    <div><p><NavLink to="/java">jsvascript</NavLink></p></div>
                    <div><p><NavLink to="/profile">profile</NavLink></p></div>
                    <div className='singout'><p onClick={()=>{
                        signOut(auth).then(() => {
                            navigate("/Singin");
                            actionsign(false);
                          }).catch((error) => {
                            // An error happened.
                          });
                    }}>sin-out</p></div>
                    </div>
 
                </div>
            </div>
        </div>
        
    );
}
export default Navparpages;
