import React from 'react';
import {
    
    NavLink
 }from "react-router-dom";
import './stylscompawnds/Stylesing.css';
import { Filecontext } from '../../conText/dataTemth';
import {useContext} from 'react';

function Navparsing (){
    const {actiontoglethemth,allData} = useContext(Filecontext);
    document.body.className = allData.themth;
    return (
        <div className="style-navsin header">
            <div className='flex-nav content'>
             <div className='logo'><p>Elpahr</p></div>
             <div className='mode'><p onClick={()=>{actiontoglethemth(allData.themth === "light" ?"Dark":"light")}}>mode</p></div>
             <div className='buttons'>
                <div><NavLink to="/singin">singin</NavLink></div>
                <div><NavLink to="/singup">singup</NavLink></div>
                
             </div> 
            </div>
        </div>
    );
}
export default Navparsing;