import React
 from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import '../styles/sass-pages/Stylehtml.scss';
import Navparpages from './compawnds/navpaepages';
import { auth } from '../firbase/config';
import { useNavigate } from "react-router-dom";

function HTML()
{   const [user, loading, error] = useAuthState(auth);
    const navigate = useNavigate();
    if(loading)
    {
        return (<>
            <div className='style-profile-fixed-loding'>
                <div className='loade'>
                    
                </div>
            </div>
            <Navparpages /></>);
    }
    if(!user){
       navigate("/singin");
    }
    if(user)
    {
        if(user.emailVerified)
        {
            return (
                <div className="style-html">
                    <Navparpages />
                    <h1>html</h1>             
                </div>
            );
        }
    }
    
}
export default HTML;