import React
 from 'react';
 import {Link }from "react-router-dom";
 import { useContext } from 'react';
import '../styles/sass-pages/Stylesingin.css';
import Navparsing from './compawnds/Navsing';  
import {useState} from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import {signInWithEmailAndPassword} from "firebase/auth";
import { auth } from '../firbase/config';
import { useNavigate } from "react-router-dom";
import { Filecontext } from '../conText/dataTemth';
import { useEffect } from 'react';


function Singin()
{
    const [user, loading, error] = useAuthState(auth);
    const {actionsign} = useContext(Filecontext);
    const [emailSin,setemailSin] = useState("");
    const [pasSinin,setpasSinin] = useState("");
    const [testacount,settestacount] = useState("");
    
    const navigate = useNavigate();
    
      if(loading)
      {
        return (
            <>
            <Navparsing />
            <div className='style-profile-fixed-loding'>
                    <div className='loade'>
                        
                    </div>
                </div>
            </>
        );
        
      }
      if(user)
      {
        if(user.emailVerified)
        {
           navigate("/html");
           actionsign(true);
        }
        if(!user.emailVerified)
        {
            return (
                <div className='style-veremail'>
        <div className='flex'>
        <p>     </p>
        <p>{user.email}</p>
        <br></br>
        <button onClick={()=>{document.location.reload();}}> </button>
        </div>
        
      </div>
            );
        }
      }
      if(!user)
      {
    return (
        <div className="style-singin">
            <Navparsing />
            <div className='absolute-form'> 
                <form>
                    <h2>elpahr sing in</h2>
                    <input type="text" placeholder="Email enter" required onChange={(eo)=>{setemailSin(eo.target.value)}}/>
                    <br/>
                    <br/>
                    <input type="password" placeholder="password enter" required onChange={(eo)=>{setpasSinin(eo.target.value)}}/>
                    <br/>
                    <br/>
                    <button onClick={(eo)=>{
                    eo.preventDefault();
                        signInWithEmailAndPassword(auth, emailSin, pasSinin)
                          .then((userCredential) => {
                            // Signed in 
                          //const user = userCredential.user;
                            
                            alert("        ")
                            settestacount("");
                            // ...
                          })
                          .catch((error) => {
                            const errorCode = error.code;
                            //const errorMessage = error.message;
                            //console.log(errorCode)
                            
                            switch(errorCode)
                            { 
                                case "auth/network-request-failed":
                                    settestacount("   ")
                                    break;
                                case "auth/user-not-found":
                                    settestacount("    ");
                                    break;
                                case "auth/wrong-password":
                                    settestacount(" ");
                                    break;
                                case "auth/user-not-found":
                                    settestacount(" ");
                                    break;
                                case "auth/invalid-email":
                                    settestacount("  ");
                                break;
                                default:
                                    return errorCode;
                                
                            }
                          });
                        
                    }}> </button>
                    <br/>
                    <p>dont have an acount<Link to="/singup">Sing-Up</Link> </p>
                    <p>{testacount}</p>
                </form>
            </div>
        </div>
    );}
}
export default Singin;