import React from 'react';
/* style home */
import '../styles/sass-pages/Stylehome.scss';
/*-----------*/
/* export compawnds */
import Navpar from './compawnds/navpar';

/* --------------- */
/* share data */
import {useContext} from 'react';
import {Filecontext} from '../conText/dataTemth';
/* ends share data */

function Home(){
    const {actionage,
        actioncount,
        actionmenue,
        actionname,
        actionthemth,
        actiontoglethemth,
        allData} = useContext(Filecontext);

    return (
<div className={`style-home`}> 
        
        <Navpar /> 
        <h1>welcome</h1>
        <div className="style-App">   
    <div className='stings'>
        <button onClick={()=>{actionthemth(allData.themth==="light" ?"dark":"light")}}>toggle mode</button>
        <div onClick={
            ()=>{
                actiontoglethemth();
            }
        } className={`menue`}>
            <p onClick={()=>{actionmenue()}}>menue</p>
            <div className={`open-menue ${allData.menue}`}>
                <p>welcome</p>
                <p>elpahr</p>
            </div>
        </div>
        <div className="them-mode">
            <span>icon</span>
            <div className="contrl">
                <span>dark</span>
                <span>light</span>
                <div className="but-toggle">
                    <label className='text-themth'>dark</label>
                </div>
            </div>
            <span>icon</span>
        </div>
        <div className="buttons">
        <button onClick={()=>{
            actionthemth("orange");
        }}>orange</button>
        <button onClick={()=>{actionthemth("green")}}>green</button>
        <button onClick={()=>{actionthemth("dark")}}>dark</button>
        <button onClick={()=>{actionthemth("light")}}>white</button> 
        </div>   
        <h1>myname is {allData.myname}</h1>
        <button onClick={()=>{actionname()}}>change name </button>
                
                                
        <h1>my age is{allData.myage}</h1>
        <button onClick={()=>{actionage()}}>change age</button>
        <button onClick={()=>{actioncount()}}>count{allData.count}</button>
    </div>
    </div> 
        
    </div>
    
        
    );
}
export default Home;