import '../styles/sass-pages/Styleppritndata.css';
import Navpar from './compawnds/navpar';
import React from 'react';
/* export share data */
import { useContext} from 'react';
import  {Filecontext}  from '../conText/dataTemth';

/* ends */
function Printdat(){
    const {allData} = useContext(Filecontext);
    return (
        <div className={`style-printdata`}>
            
            <Navpar />
            <div className={`outdata`}>
                <p>myname is{allData.myname}</p>
                <p>myage {allData.myage}</p>
            </div>
            
        </div>
    );
}
export default Printdat;